<template>
  <div class="MonHocToan">

    <div>
      <!-- User Interface controls -->
      <!-- Main table element -->
      <form @submit.stop.prevent="onSave">
        <b-table thead-class="myTable" ref="mytable" :items="myProvider" :fields="fields" :current-page="currentPage"
          :per-page="perPage" :filter="filter" :filter-included-fields="filterOn" :sort-by.sync="sortBy"
          :sort-desc.sync="sortDesc" :sort-direction="sortDirection" stacked="sm" show-empty small fixed outlined
          bordered @filtered="onFiltered">
          <template v-slot:cell()="{ value, item, field: { key } }">
            <template v-if="sel[pri] != item[pri]">
              <template v-if=" item[pri]!=0 " >
                <template v-if=" key==pri ">
                  {{value}}
                </template>
                <template v-if=" key=='MonId' ">
                  {{value}}
                </template>
                <template v-if=" key=='DonGia' ">
                  {{value}}
                </template>
                <template v-if=" key=='SoLg' ">
                  {{value}}
                </template>
                <template v-if=" key=='Tien' ">
                  {{value}}
                </template>
                <template v-if=" key=='TrangThai' ">
                  {{value}}
                </template>
              </template>
            </template>
            <template v-if="(sel[pri] == item[pri] || item[pri] == 0)" >
              <template v-if="key == 'Id'">
                <span>{{ value }}</span>
              </template>
              <template v-if="key == 'MonId'">
                <!-- <b-form-input v-model="item[key]" :name="key" v-validate="'required'" placeholder=""
                  :class="{ 'ko-hop-le': errors.has(key) }">
                </b-form-input> -->

                <!-- <VueTypeaheadBootstrap
                  v-model="item[key]"
                  :name="key"
                  v-validate="'required'"
                  :data="ListAuto"
                  :serializer="(item) => item.Name"
                  @input="OnSearch(item[key])"
                  :class="{ 'ko-hop-le': errors.has(key) }"
                >
                </VueTypeaheadBootstrap> -->

                <v-select @input="onChangeMonId(item)" v-model="item[key]" v-validate="'required'" :name="key" :reduce="(option) => option.id"
                      :options="
                        mons
                      " :has-error="errors.has(key)" :class="{ danger: errors.has(key) }" />
              </template>

                <!-- // "Id":1101,
        //     "MonId":71,
        //     "DonGia":17000,"SoLg":11,"Tien":187000,"BanId":1284,"TrangThai":true},{"Id":1107,"           -->

              <template v-if="key == 'DonGia'">
                {{ value }}
              </template> 
              <template v-if="key == 'SoLg'">
                <b-form-input @input="SoLgChange(item)" v-model="item[key]" :name="key" v-validate="'required'" placeholder=""
                  :class="{ 'text-right':true,'ko-hop-le': errors.has(key) }">
                </b-form-input>
              </template> 
              <template v-if="key == 'Tien'">
                {{ value }}
              </template> 
              <template v-if="key == 'TrangThai'">
                <b-form-checkbox class="my-checkbox" v-model="item[key]" :name="key" value=true
                      unchecked-value=false>
                    </b-form-checkbox>
              </template> 

            </template>
            <!-- { "id": 1, "firstName": "", "lastName": "", "age": 65, "active": true } -->
          </template>
          <template #cell(cmd)="row">
            <template v-if="row.item[pri] == 0">
              <b-button class="mr-1" size="sm" @click="onSave(row.item)">
                <b-icon-hand-thumbs-up></b-icon-hand-thumbs-up>
              </b-button>
              <b-button class="mr-1" size="sm" @click="OnCancel(row.item)">
                <b-icon-file-x></b-icon-file-x>
              </b-button>
            </template>
            <template v-else>
              <b-button variant="outline" class="mr-1" v-if="sel[pri] != row.item[pri]" size="sm"
                @click="OnEdit(row.item)">
                <b-icon-pencil-square></b-icon-pencil-square>
              </b-button>
              <b-button variant="outline" class="mr-1" v-if="sel[pri] != row.item[pri]" size="sm"
                @click="OnDel(row.item)">
                <b-icon-trash></b-icon-trash>
              </b-button>
              <b-button variant="outline" class="mr-1" v-if="sel[pri] == row.item[pri]" size="sm"
                @click="onSave(row.item)">
                <b-icon-hand-thumbs-up></b-icon-hand-thumbs-up>
              </b-button>
              <b-button variant="outline" class="mr-1" v-if="sel[pri] == row.item[pri]" size="sm"
                @click="OnCancel(row.item)">
                <b-icon-file-x></b-icon-file-x>
              </b-button>
            </template>
          </template>

          <template #bottom-row="data">
            <td :colspan="data.columns">
              <b-row>
                <b-col sm="6" class="my-1">
                  <b-form-group label="Tìm kiếm" label-for="filter-input" label-cols-sm="3" label-align-sm="right"
                    label-size="sm" class="mb-0">
                    <b-input-group size="sm">
                      <b-form-input id="filter-input" v-model="filter" type="search" placeholder="Type to Search">
                      </b-form-input>

                      <b-input-group-append>
                        <b-button :disabled="!filter" @click="filter = ''">Clear</b-button>
                      </b-input-group-append>
                    </b-input-group>
                  </b-form-group>
                </b-col>
                <b-col sm="6" class="my-1">
                  <b-form-group label="Số dòng" label-for="per-page-select" label-cols-sm="6" label-cols-md="4"
                    label-cols-lg="3" label-align-sm="right" label-size="sm" class="mb-0">
                    <b-form-select id="per-page-select" v-model="perPage" :options="pageOptions" size="sm">
                    </b-form-select>
                  </b-form-group>
                </b-col>

                <b-col sm="6" class="my-1 text-right">
                  <span>page : {{ currentPage }} / {{last_page}} of {{totalRows}} </span>
                  <b-button :disabled="adding == true || isEditing() == true" class="mr-1 mb-1" size="sm" @click="OnAdd">
                    <b>Add</b></b-button>
                </b-col>
                <b-col sm="6" class="my-1">
                  <b-pagination ref="mypage" v-model="currentPage" :total-rows="totalRows" :per-page="perPage" size="sm"
                    align="fill" @change="onChangePage" @input="onPagingInput" first-text="First" prev-text="Prev"
                    next-text="Next" last-text="Last">
                  </b-pagination>
                </b-col>
              </b-row>

            </td>
          </template>

          <template #row-details="row">
            <b-card>
              <ul>
                <li v-for="(value, key) in row.item" :key="key">{{ key }}: {{ value }}</li>
              </ul>
            </b-card>
          </template>
        </b-table>

      </form>
      <!-- Info modal -->
      <b-modal :id="infoModal.id" :title="infoModal.title" ok-only @hide="resetInfoModal">
        <pre>{{ infoModal.content }}</pre>
      </b-modal>
    </div>
  </div>
</template>
<script>

import VeeValidate from "vee-validate";
import Vue from "vue";
Vue.use(VeeValidate, {
  classes: true,
  fieldsBagName: 'veeFields'
});

import VueTypeaheadBootstrap from "vue-typeahead-bootstrap";
import { debounce } from "lodash";

import Repository from "@/Repository"

import axios from 'axios'
export default {
  props:["BanId"],
  components:{
    VueTypeaheadBootstrap
  },

  data() {
    return {
      items: [
      ],
      fields: [
        {
          key: 'Id',
          formatter: (value, key, item) => {
            return value;
          },
          sortable: true,
          thClass: 'text-center',
          tdClass: 'text-center align-middle',
          thStyle: { width: "10%" },
        },
        {key: 'MonId',
        //[ { "Id": 4, "Name": "Trà ô long", "DonGia": 1500 },
        formatter: (value, key, item) => {
            let kq = "";
            //alert(JSON.stringify(this.mons))
            //[{"id":4,"label":"Trà ô long","DonGia":1500},{"id":65,"label":"Trà Đá","DonGia":2000},{"id":7                      
            let index = this.mons.findIndex(x => x.id == value);
            if (index >= 0) {
              kq = this.mons[index].label;
            }
            return kq;
          },

        sortable: true,thClass: 'text-center',tdClass: 'align-middle',thStyle: { width: "40%" },},
        {key: 'DonGia',
          formatter: (value, key, item) => {return parseInt(value).toLocaleString();},
          sortable: true,thClass: 'text-center',tdClass: 'align-middle text-right',thStyle: { width: "10%" },},
        {key: 'SoLg',sortable: true,thClass: 'text-center',tdClass: 'align-middle text-right',thStyle: { width: "10%" },},
        {key: 'Tien',
          formatter: (value, key, item) => {return parseInt(value).toLocaleString();},
          sortable: true,thClass: 'text-center',tdClass: 'align-middle text-right',thStyle: { width: "10%" },},
        {key: 'TrangThai',sortable: true,thClass: 'text-center',tdClass: 'align-middle text-center',thStyle: { width: "10%" },},
        {
          key: 'cmd',
          class: 'text-center',
          thClass: 'text-center',
          tdClass: 'align-middle',
          thStyle: { width: "10%" }
        }

      ],
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15, { value: 100, text: "Show a lot" }],
      sortBy: '',
      sortDesc: false,
      sortDirection: 'asc',
      filter: null,
      filterOn: [],
      infoModal: {
        id: 'info-modal',
        title: '',
        content: ''
      },

      sel: {},
      pri: "Id",
      last_page: 0,
      adding: false,
      ListAuto:[],
      mons:[]

    }
  },

  watch: {
    last_page: function (newValue, oldValue) {
    },
    currentPage: function (val) {
    },
    totalRows: function (newValue, oldValue) {
    }
  },
  computed: {
  },
  mounted() {
    this.getMons().then(res=>{
      //alert(JSON.stringify(res));
      //{"data":[{"Id":4,"Name":"Trà ô long","DonGia":1500,"
      //[{"id":4,"label":"Trà ô long","DonGia":1500},{"id":65,"label":"Trà Đá","DonGia":2000},{"id":7      
      this.mons=[];
      res.data.forEach(x => {
        this.mons.push({
          id:x.Id,
          label:x.Name,
          DonGia:x.DonGia
        })
      });
    })
  },
  methods: {

    SoLgChange(item){
      let DonGia=item.DonGia;
      let SoLg=item.SoLg;
      let Tien=DonGia*SoLg;
      item.Tien=Tien;
    },    
    onChangeMonId(item){
      let index=this.mons.findIndex(x=>x.id==item.MonId);
      let DonGia=this.mons[index].DonGia;
      let SoLg=item.SoLg;
      let Tien=DonGia*SoLg;
      item.Tien=Tien;
      item.DonGia=DonGia;
    }, 


    OnSearch: debounce(function (query) {
        fetch(`/api/Ban/AutoComplete?q=${query}`)
          .then((response) => {
            return response.json();
          })
          .then((data) => {
            console.log( JSON.stringify( data.items))
            this.ListAuto = data.items;
          });
      }, 500),
    onPagingInput() {
    },
    myProvider(ctx) {
      let currentPage = ctx.currentPage;
      let perPage = ctx.perPage;
      let sortBy = ctx.sortBy;
      let sortDesc = ctx.sortDesc;
      let vm = this;
      let url = `/api/BanChiTiets/GetPage2ByBanId`;
      url = `${url}?page=${currentPage}&size=${perPage}&BanId=${this.BanId}&sortBy=${sortBy}&sortDesc=${sortDesc}`;
      if (ctx.filter) {
        let filter = ctx.filter;
        url = `${url}&filter=${filter}`;
      }
      return axios.get(url)
        .then(res => {
          //alert(JSON.stringify(res));
          /*{"data":{"last_page":1,"rowCount":3,"data":[{
            "Id":1101,
            "MonId":71,
            "DonGia":17000,"SoLg":11,"Tien":187000,"BanId":1284,"TrangThai":true},{"Id":1107,"          
          */
          vm.last_page = res.data.last_page;
          vm.totalRows = res.data.rowCount;
          if (this.adding == true) {
            let r = {};
            r[this.pri] = 0;
            r["MonId"] = null;
            r["DonGia"] = 1;
            r["SoLg"] = 1;
            r["Tien"]=1;
            r["BanId"]=this.BanId;
            r["TrangThai"]=false;
            res.data.data.unshift(r);
          }
          return vm.mapData(res.data.data); //res.data.data;
        })
    },
    mapData(data) {
      let kq = [];
      data.forEach(obj => {
        let x = {};
        Object.keys(obj).forEach(key => {
          let k = key;
          x[k] = obj[key];
        });
        kq.push(x);
      })
      return kq;
    },
    onChangePage() {
      this.sel = {};
      this.adding = false;
    },
    OnAdd() {
      this.adding = true;
      this.$refs.mytable.refresh();
    },
    OnEdit(item) {
      if (JSON.stringify(this.sel) != "{}" || this.adding == true) {
        return;
      }
      this.sel = JSON.parse(JSON.stringify(item));
    },
    onSave(item) {
      this.$validator.validateAll().then((result) => {
        if (!result) {
          this.toast("Không hợp lệ");
          return;
        }
        let r = JSON.parse(JSON.stringify(item));
        //alert(JSON.stringify(r));
        //return;
        this.Save(r).then(res => {
          //alert(JSON.stringify(res));
          if(res.data.kq!="ok"){
            this.toast(`${res.data.msg}`);
            return;
          }
          if(item.pri==0){
            if(res.data.kq=="ok"){
            }
          }else{
            this.sel = {};
          }
          this.toast(`${res.data.msg}`);
          this.adding = false;
          this.$refs.mytable.refresh();
        })
      });
    },
    OnCancel(item) {
      if (item[this.pri] != 0) {
        this.doCancel(item);
        this.sel = {};
      } else {
        this.adding = false;
        this.$refs.mytable.refresh();
      }
    },
    OnDel(item) {
      this.Del(item[this.pri]).then(res => {
        //alert(JSON.stringify(res));
        //return;
        if(res.data.kq=='nook'){
          this.toast(`${res.data.msg}`);
          return;
        }
        this.toast(`${res.data.msg}`);
        let lastPage = this.getLastPage(this.totalRows - 1);
        if (lastPage < this.currentPage) this.currentPage = lastPage;
        this.$refs.mytable.refresh();
      })
    },
    delRow(item) {
      let id = item.id;
      let index = this.items.findIndex(x => x.id == id);
      this.items.splice(index, 1);
    },
    isEditing() {
      return JSON.stringify(this.sel) != "{}"
    },
    isAdded() {
      let kq = false;
      this.items.forEach(x => {
        if (x.id == 0) kq = true;
      })
      return kq;
    },
    doCancel(item) {
      for (let key in item) {
        if (item[key] != this.sel[key]) {
          item[key] = this.sel[key]
        }
      }
    },
    getLastPage(count) {
      let pageSize = this.perPage;//this.tabulator.getPageSize();
      let du = count % pageSize;
      return du == 0 ? count / pageSize : (count - du) / pageSize + 1;
    },
    gotoPage(page) {
      this.currentPage = page;
      this.$refs.mytable.refresh();
    },

    toast(msg, variant = null) {
      this.$bvToast.toast(msg, {
        title: `Information`,
        variant: variant,
        solid: true,
      });
    },

    async Save(r) {
      return await Repository.post(`/api/BanChiTiets/Save`, r).catch((err) => {
        console.log(err);
      });
    },
    async Del(id) {
      return await Repository
        .get(`/api/BanChiTiets/Delete?id=${id}`)
        .catch((err) => {
          console.log(err);
        });
    },
    async getMons() {
      return await Repository.get(`/api/Mon/GetAll`).catch((err) => {
        console.log(err);
      });
    },
    async getById(id) {
      return await Repository.get(`/api/Ban/GetById?id=${id}`).catch((err) => {
        console.log(err);
      });
    },
    async GetCombo() {
      return await Repository.get(`/api/Ban/GetCombo`).catch((err) => {
        console.log(err);
      });
    },
    info(item, index, button) {
      this.infoModal.title = `Row index: ${index}`
      this.infoModal.content = JSON.stringify(item, null, 2)
      this.$root.$emit('bv::show::modal', this.infoModal.id, button)
    },
    resetInfoModal() {
      this.infoModal.title = ''
      this.infoModal.content = ''
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length
      this.currentPage = 1
    }
  }

}
</script>
<style lang="scss">
@import "@/assets/scss/myStyle.scss";
</style>

